const initialState = {
    useDefaultLayout: true,
    personalLayout: getWidgets(),
}

const userDashboardReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'ADD_WIDGET':
            var updatedWidgets =
                state.personalLayout.map(widget => {
                    if (widget.id !== action.payload.id) {
                        return widget
                    }
                    return {
                        ...widget,
                        ...action.payload.widget
                    }
                })

            saveToLS(updatedWidgets);

            return { ...state, useDefaultLayout: false, personalLayout: updatedWidgets }

        case 'TOGGLE_DEFAULT':
            saveToLS(action.payload.defaultLayout);
            return { ...state, useDefaultLayout: action.payload.useDefault, personalLayout: action.payload.defaultLayout }
        default:
            return state;
    }
}

function getWidgets() {
    var storedSetup = getStoredWidgets();

    // if (!storedSetup || !storedSetup.length) {
    //     return [];
    // }

    return storedSetup;
}

function getStoredWidgets() {
    let ls = {};
    if (global.localStorage) {
        try {
            ls = JSON.parse(global.localStorage.getItem("Userlayouts")) || {};
        } catch (e) {
            /*Ignore*/
        }
    }
    return ls;
}

function saveToLS(value) {
    if (global.localStorage) {
        global.localStorage.setItem(
            "Userlayouts",
            JSON.stringify(value)
        );
    }
}

export default userDashboardReducer;