import React from 'react';
import Card from 'react-bootstrap/Card';
import Form from 'react-bootstrap/Form';

import { connect } from 'react-redux';

import { updateDefault, selectWidget } from "../../redux/actions";

import _ from "lodash";
import GridLayout from 'react-grid-layout';

class MiniDashboard extends React.Component {
    static defaultProps = {
        className: "layout",
        rowHeight: 60,
        onLayoutChange: function () { },

        verticalCompact: true
    };

    state = {
        active: false,
        currentBreakpoint: "lg",
        // compactType: null,
        mounted: false,
        layouts: { lg: this.getWidgetLayouts() },
    };

    componentDidMount() {
        this.setState({ mounted: true });
    }

    getWidgetLayouts() {
        return this.props.defaultWidgets.map(widget => { return widget.layout });
    }

    generateWidgetLayouts() {
        return _.map(this.getWidgetLayouts(), function (widget, i) {
            return widget.layout;
        });
    }

    isWidgetStatic = widget=> {
        if(widget.layout.static){
            return <p>STATIC</p>
        }
    }

    generateDOM() {
        return this.props.defaultWidgets.map(widget => {
            return (
                <Card key={widget.layout.i} className={widget.layout.static ? "static" : ""}>
                    <Card.Body>
                        <h5>{widget.name}</h5>
                        {this.isWidgetStatic(widget)}
                        {/* <div className="hide-button" onClick={() => this.handleUpdateWidget(widget)}>
                            &times;
                        </div> */}
                    </Card.Body>
                </Card>
            );
        })
    }

    onLayoutChange = (layouts) => {
        layouts.forEach(layout => {
            var widget = this.props.defaultWidgets.find(widget => layout.i === widget.layout.i);
            widget.layout = layout;
            
            if(this.props.selectedWidget){
                if(widget.id === this.props.selectedWidget.id){
                    this.props.selectWidget(null);
                    this.props.selectWidget(widget);
                }
            }
            
            this.props.updateDefault(widget);
        });
    };

    onNewLayout = () => {
        this.setState({
            layouts: { lg: this.getWidgetLayouts() }
        });
    };

    handleUpdateWidget = widget => {
        widget.inUse = !widget.inUse;
        this.props.updateDefault(widget);
    };

    render() {
        return (
            <div className="d-flex flex-row border-bottom p-3">
                <div className="mr-3">
                    <h2 className="">Dashboard Layouts</h2>
                    <p>Setup the default dashboard layout</p>
                    <Form>
                        <fieldset>
                            <Form.Group>
                                    <Form.Check
                                        defaultChecked
                                        type="radio"
                                        label="Desktop layout"
                                        name="formHorizontalRadios"
                                        id="formHorizontalRadios1"
                                    />
                                    <Form.Check
                                        disabled
                                        type="radio"
                                        label="Tablet layout"
                                        name="formHorizontalRadios"
                                        id="formHorizontalRadios2"
                                    />
                                    <Form.Check
                                        disabled
                                        type="radio"
                                        label="Mobile layout"
                                        name="formHorizontalRadios"
                                        id="formHorizontalRadios3"
                                    />
                            </Form.Group>
                        </fieldset>
                    </Form>
                </div>
                <div className="border mini-dashboard justify-content-center">
                    <GridLayout className="" layout={this.getWidgetLayouts()} cols={12} rowHeight={10} width={550} onLayoutChange={this.onLayoutChange}>
                        {this.generateDOM()}
                    </GridLayout>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    var defaultWidgets = state.defaultDashboard.widgets.filter(widget => widget.inUse === true);
    var selected = state.selectedWidget.selected;
    var dashboardLayout = state.dashboardCols;

    return { defaultWidgets: defaultWidgets, cols: dashboardLayout, selectedWidget: selected }
}

export default connect(
    mapStateToProps,
    {
        updateDefault: updateDefault,
        selectWidget: selectWidget
    }
)(MiniDashboard);