import React, { Component } from "react";
import BackendWidgetList from "../components/backend/widget-list";
import MiniDashboard from "../components/backend/mini-dashboard";
import WidgetSettings from "../components/backend/widget-settings";

export default class Backend extends Component {

  ifWidgetSelected(){
    if (!this.props.widgets || !this.props.widgets.length) {
      return (
        <WidgetSettings></WidgetSettings>
      );
    }
  }

  render() {
    return (
      <div className="h-100">
        <div className="">
          <MiniDashboard></MiniDashboard>
        </div>
        <div className="h-100 d-flex flex-row">
          <div className="border border-top-0">
            <BackendWidgetList></BackendWidgetList>
          </div>
          <div className="">
            {this.ifWidgetSelected()} 
          </div>
        </div>
        
      </div>
    );
  }
}