export function addWidget(widget) {
    return {
        type: 'ADD_WIDGET',
        payload: {
            id: widget.id,
            widget
        }
    }
};

export function updateDefault(widget) {
    return {
        type: 'UPDATE_DEFAULT',
        payload: {
            id: widget.id,
            widget
        }
    }
};

export function setDefault(isDefault, defaultLayouts) {
    return {
        type: 'TOGGLE_DEFAULT',
        payload: {
            useDefault: isDefault,
            defaultLayout: defaultLayouts
        }
    }
};

export function selectWidget(widget) {
    return {
        type: 'SELECTED_WIDGET',
        payload: {
            widget
        }
    }
};