import React from 'react';
import Card from 'react-bootstrap/Card';
import Accordion from 'react-bootstrap/Accordion';
import { Col, Form } from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEllipsisH, faPlus } from '@fortawesome/free-solid-svg-icons';
import { addWidget } from "../../redux/actions";
import { connect } from 'react-redux';

class ToolBoxItem extends React.Component {

  // handleChange(event) {
  //   event.persist();

  //   this.props.handleToUpdate(this.state)
  // }

  handleAddWidget = widget => {
    widget.inUse = !widget.inUse;
    this.props.addWidget(widget);
  };

  render() {
    //   var widget = toolboxlWidgets.find(x => {return x.id.toString() === this.props.item.i} );
    return (
      <Card className="widget-list">
        <Card.Header>
          <div className="d-flex flex-row justify-content-between">
            {/* <Col><Button as="FontAwesomeIcon" icon={faEllipsisH} onClick={this.props.onTakeItem.bind(undefined, this.props.item)}>+</Button></Col> */}
            {/* <div><FontAwesomeIcon icon={faPlus} className="fa-2x" onClick={this.props.onTakeItem.bind(undefined, this.props.item)} /></div> */}
            <div><FontAwesomeIcon icon={faPlus} className="fa-2x" onClick={ () => this.handleAddWidget(this.props.widget)} /></div>

            <div><h5>{this.props.widget.name}</h5></div>
            <div>
              <Accordion.Toggle className="settings-btn" eventKey={this.props.widget.id}>
                <FontAwesomeIcon icon={faEllipsisH} className="fa-2x" />
              </Accordion.Toggle>
            </div>
          </div>
        </Card.Header>
        <Accordion.Collapse eventKey={this.props.widget.id}>
          <Card.Body>
            Settings
              <Form onSubmit={this.handleSubmit}>
              <Form.Row>
                <Form.Group as={Col} controlId="formGridXPosition">
                  <Form.Label>X Position</Form.Label>
                  <Form.Control size="sm" type="number" readOnly value={this.props.widget.layout.x} />
                </Form.Group>

                <Form.Group as={Col} controlId="formGridYPosition">
                  <Form.Label>Y Position</Form.Label>
                  <Form.Control size="sm" type="number" readOnly value={this.props.widget.layout.y} />
                </Form.Group>
              </Form.Row>
            </Form>
          </Card.Body>
        </Accordion.Collapse>
      </Card>
    );
  }
}

export default connect(
  null,
  {
    addWidget: addWidget
  }
)(ToolBoxItem);