import userDashboardReducer from './userDashboard';
import defaultDashboardReducer from './defaultDashboard';
import selectedWidgetReducer from './selectedWidget';

import {combineReducers} from 'redux';

const allReducers = combineReducers({
    userDashboard: userDashboardReducer,
    defaultDashboard: defaultDashboardReducer,
    selectedWidget: selectedWidgetReducer
});

export default allReducers;