import React from "react";
import ChartWidget from './chart/chart-widget.jsx'
import TestWidget1 from './test-widget.jsx'
import CarouselWidget from './carousel/carousel-widget.jsx'

const Components = {
  chartWidget: ChartWidget,
  testwidget: TestWidget1,
  carouselWidget: CarouselWidget
};

export default block => {
  // component does exist
  if (typeof Components[block.component] !== "undefined") {
    return React.createElement(Components[block.component], {
      key: block.id,
      block: block
    });
  }
  // component doesn't exist yet
  return React.createElement(
    () => <div>The component {block.component} does not exist.</div>,
    { key: block.id}
  );
}