import React from 'react';
import Accordion from 'react-bootstrap/Accordion';
import Button from 'react-bootstrap/Button';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft } from '@fortawesome/free-solid-svg-icons'
import { connect } from 'react-redux';

import ToolBoxItem from './toolbox-item'

class ToolBox extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false
    };
  }

  toggleToolbox() {
    this.setState({ open: !this.state.open })
  }

  ifToolboxEmpty(){
    if (!this.props.widgets || !this.props.widgets.length) {
      return (
        <h4 className="text-white text-center">Empty</h4>
      );
    }
  }
  
  render() {
    return (
      <div className={`d-flex flex-row widgetmenu`}>
        <Button variant="" className="open-widget-btn p-1"
          onClick={() => this.toggleToolbox()}
          aria-controls="Open widget List"
          aria-expanded={this.state.open}>
          {/* <span className="horizontal-btn-text">Widgets</span> */}
          <FontAwesomeIcon icon={faChevronLeft} className={`fa-2x text-white ${this.state.open ? "rotate180" : ""}`} />
        </Button>
        <div className={`widget-toolbox ${this.state.open ? "toolbox-open" : "toolbox-closed"}`}>
          <div className="toolbox-header">
            <h3 className="text-white text-center">Toolbox</h3>
          </div>
          <Accordion className="flex-grow w-100 widget-list">
            {this.props.widgets.sort((a, b) => a.name.localeCompare(b.name)).map(item => (
                <ToolBoxItem
                  key={item.id}
                  widget={item}
                />
              ))}
          </Accordion>
          {this.ifToolboxEmpty()}
        </div >
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  var toolboxWidgets = []
  if(state.userDashboard.useDefaultLayout){
    toolboxWidgets = state.defaultDashboard.widgets.filter(widget => widget.inUse === false).sort((a, b) => a.name.localeCompare(b.name));
  }
  else{
    toolboxWidgets = state.userDashboard.personalLayout.filter(widget => widget.inUse === false).sort((a, b) => a.name.localeCompare(b.name));
  }


  return { widgets: toolboxWidgets }
}

export default connect(mapStateToProps)(ToolBox);