import React, { Component } from "react";
import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import Home from "./home";
import Dashboard from "./dashboard";
import Backend from "./backend";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faCogs, faGripHorizontal } from '@fortawesome/free-solid-svg-icons';

export default class Main extends Component {
  render() {
    return (
      <HashRouter>
        <div>

          <div className="sidenav">
            <NavLink to="/">
              <FontAwesomeIcon icon={faHome} className="navIcon"/>
              <span className="ml-2">Home</span>
            </NavLink>
            <NavLink to="/dashboard">
              <FontAwesomeIcon icon={faGripHorizontal} className="navIcon"/>
              <span className="ml-2">Dashboard</span>
            </NavLink>
            <NavLink to="/backend-view" className="">
              <FontAwesomeIcon icon={faCogs} className="navIcon"/>
              <span className="ml-2">Settings</span>
            </NavLink>
          </div>
          {/* <div className="header">
            <h3 className="align-text-bottom">Dashboard App</h3>
          </div> */}
          <div className="main">
            <div className="content">
              <Route exact path="/" component={Home} />
              <Route path="/dashboard" component={Dashboard} />
              <Route path="/backend-view" component={Backend} />
            </div>
          </div>
        </div>
      </HashRouter>
    );
  }
}