const defaultDashboardReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'UPDATE_DEFAULT':
            var updatedDefault =
                state.widgets.map(widget => {
                    if (widget.id !== action.payload.id) {
                        return widget
                    }
                    return {
                        ...widget,
                        ...action.payload.widget
                    }
                })

            initialState.widgets = updatedDefault;

            return { ...state, widgets: updatedDefault }   
        default:
            return state;
    }
}

export default defaultDashboardReducer;