import React from 'react';
import ListGroup from 'react-bootstrap/ListGroup';

import { selectWidget } from "../../redux/actions";


import { connect } from 'react-redux';

class BackendWidgetList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false
        };
    }

    toggleToolbox() {
        this.setState({ open: !this.state.open })
    }

    ifListEmpty() {
        if (!this.props.widgets || !this.props.widgets.length) {
            return (
                <h4 className="text-white text-center">Empty</h4>
            );
        }
    }

    handleAddWidget = widget => {
        if(this.isSelected(widget.id)){
            this.props.selectWidget(null);
        }
        else{
            this.props.selectWidget(widget);
        }
    };

    isSelected(id){
        if(this.props.selectedWidget){
            return id === this.props.selectedWidget.id;
        }
    }

    render() {
        return (
            <div className="h-100 bg-white">
                <ListGroup className="border-0 rounded-0">
                    {this.props.widgets.sort((a, b) => a.id - b.id).map(item => (
                        <ListGroup.Item className="border-top-0 border-right-0 border-left-0 rounded-0" key={item.id} active={this.isSelected(item.id)} onClick={() => this.handleAddWidget(item)}>{item.name}</ListGroup.Item>
                    ))}
                </ListGroup>,
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    const widgets = state.defaultDashboard.widgets.sort((a, b) => a.name.localeCompare(b.name));
    const selected = state.selectedWidget.selected;

    return { widgets: widgets, selectedWidget: selected }
}

export default connect(
    mapStateToProps,
    {
        selectWidget: selectWidget
    }
)(BackendWidgetList);