import React from 'react';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';

import Components from "../components.js";
import ToolBox from '../toolbox/widget-toolbox';

import { connect } from 'react-redux';

import { addWidget, setDefault } from "../../redux/actions";

import _ from "lodash";
import { Responsive, WidthProvider } from "react-grid-layout";

const ResponsiveReactGridLayout = WidthProvider(Responsive);

class DashboardBasicGridLayout extends React.Component {
  static defaultProps = {
    className: "layout",
    rowHeight: 60,
    onLayoutChange: function () { },
    
    verticalCompact: true
  };

  state = {
    active: false,
    currentBreakpoint: "lg",
    // compactType: null,
    mounted: false,
    layouts: { lg: this.getWidgetLayouts() },
  };

  componentDidMount() {
    this.setState({ mounted: true });
  }

  clearLS() {
    // global.localStorage.removeItem('Userlayouts')
    this.props.setDefault(true, this.props.defaultLayout);
    // this.props.addWidget(this.props.defaultLayout);
    // window.location.reload(false);
    
  }

  getWidgetLayouts() {
    return this.props.widgets.map(widget => { return widget.layout });
  }

  generateWidgetLayouts() {
    return _.map(this.getWidgetLayouts(), function (widget, i) {
      return widget.layout;
    });
  }

  generateDOM() {
    return this.props.widgets.map(widget => {
      return (
        <Card key={widget.layout.i} className={widget.layout.static ? "static" : ""}>
          <Card.Header>
            <span>{widget.name}</span>
            <div className="hide-button" onClick={() => this.handleUpdateWidget(widget)}>
              &times;
                </div>
          </Card.Header>
          <Card.Body className="overflow-hidden">
            {Components(widget)}
          </Card.Body>
        </Card>
      );
    })
  }

  // onBreakpointChange = breakpoint => {
  //   this.setState(prevState => ({
  //     currentBreakpoint: breakpoint,
  //     toolbox: {
  //       ...prevState.toolbox,
  //       [breakpoint]:
  //         prevState.toolbox[breakpoint] ||
  //         prevState.toolbox[prevState.currentBreakpoint] ||
  //         []
  //     }
  //   }));
  // };

  // onCompactTypeChange = () => {
  //   const { compactType: oldCompactType } = this.state;
  //   const compactType =
  //     oldCompactType === "horizontal"
  //       ? "vertical"
  //       : oldCompactType === "vertical"
  //       ? null
  //       : "horizontal";
  //   this.setState({ compactType });
  // };

  onLayoutChange = (layouts) => {
    layouts.forEach(layout => {
      var widget = this.props.widgets.find(widget => layout.i === widget.layout.i);
      widget.layout = layout;

      this.props.addWidget(widget);
    });
  };

  onNewLayout = () => {
    this.setState({
      layouts: { lg: this.getWidgetLayouts() }
    });
  };

  handleUpdateWidget = widget => {
    widget.inUse = !widget.inUse;
    this.props.addWidget(widget);
  };

  render() {
    return (
      <div className="d-flex flex-row">
        <div className="flex-fill dashboardGrid main-dashboard ml-4 pr-1 mr-5 mt-3" >
          <div className="my-3">
            <Button disabled={this.props.useDefault ? true : false} onClick={() => this.clearLS()}>Reset Layout</Button>
          </div>
          <ResponsiveReactGridLayout
            {...this.props}
            layouts={{ lg: this.getWidgetLayouts() }}
            cols={this.props.cols}
            onBreakpointChange={this.onBreakpointChange}
            onLayoutChange={this.onLayoutChange}
            // onResize={this.onResize}
             measureBeforeMount={true}
            // useCSSTransforms={this.state.mounted}
          // compactType={this.state.compactType}
          // preventCollision={!this.state.compactType}
          >
            {this.generateDOM()}
          </ResponsiveReactGridLayout>
        </div>
        <div className="px-0 ml-2">
          <ToolBox />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  var dashboardWidgets = []
  var useDefault = state.userDashboard.useDefaultLayout;
  if(useDefault){
    dashboardWidgets = state.defaultDashboard.widgets.filter(widget => widget.inUse === true);
  }
  else{
    dashboardWidgets = state.userDashboard.personalLayout.filter(widget => widget.inUse === true);
  }
  var dashboardLayout = state.dashboardCols;

  return { widgets: dashboardWidgets, cols: dashboardLayout, useDefault: useDefault, defaultLayout: state.defaultDashboard.widgets }
}

export default connect(
  mapStateToProps,
  {
    addWidget: addWidget,
    setDefault: setDefault
  }
)(DashboardBasicGridLayout);