import React from 'react';
import Button from 'react-bootstrap/Button';
import { Col, Form } from "react-bootstrap";
import { updateDefault, selectWidget } from "../../redux/actions";

import { connect } from 'react-redux';

class WidgetSettings extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            added: this.checkInUse()
        };
    }

    handleAddWidget = widget => {
        widget.inUse = !widget.inUse;
        this.setState({ added: widget.inUse })
        this.props.selectWidget(widget);
        this.props.updateDefault(widget);
    };

    getSettingsTitle() {
        if (!this.props.selectedWidget) {
            return (
                <h2 className="">No Widget Selected</h2>
            );
        }
        return (
            <h2 className="">{this.props.selectedWidget.name} Settings</h2>
        );
    }

    checkInUse() {
        if (this.props.selectedWidget) {
            return this.props.selectedWidget.inUse ? "Remove Widget" : "Add Widget";
        }
    }

    updateMinWidth = event => {
        var widget = this.props.selectedWidget;
        var minW = parseInt(event.target.value);

        widget.layout.minW = minW;
        widget.layout.w = minW > widget.layout.w ? minW : widget.layout.w;
        widget.layout.maxH = minW > widget.layout.maxW ? minW : widget.layout.maxW;

        this.props.selectWidget(null);
        this.props.selectWidget(widget);
        this.props.updateDefault(widget);
    }

    updateMaxWidth = event => {
        var widget = this.props.selectedWidget;
        var maxW = parseInt(event.target.value);

        widget.layout.maxW = maxW;
        widget.layout.w = maxW < widget.layout.w ? maxW : widget.layout.w;
        widget.layout.minH = maxW < widget.layout.minW ? maxW : widget.layout.minW;

        this.props.selectWidget(null);
        this.props.selectWidget(widget);
        this.props.updateDefault(widget);
    }

    updateMinHeight = event => {
        var widget = this.props.selectedWidget;
        var minH = parseInt(event.target.value);

        widget.layout.minH = minH;
        widget.layout.h = minH > widget.layout.h ? minH : widget.layout.h;
        widget.layout.maxH = minH > widget.layout.maxH ? minH : widget.layout.maxH;

        console.log(widget);

        this.props.selectWidget(null);
        this.props.selectWidget(widget);
        this.props.updateDefault(widget);
    }

    updateMaxHeight = event => {
        var widget = this.props.selectedWidget;
        var maxH = parseInt(event.target.value);

        widget.layout.maxH = maxH;
        widget.layout.h = maxH < widget.layout.h ? maxH : widget.layout.h;
        widget.layout.minH = maxH < widget.layout.minH ? maxH : widget.layout.minH;

        this.props.selectWidget(null);
        this.props.selectWidget(widget);
        this.props.updateDefault(widget);
    }

    setWidgetStatic = event => {
        var widget = this.props.selectedWidget;
        var isStatic = event.target.checked;

        widget.layout.static = isStatic;
        widget.layout.isDraggable = !isStatic;
        widget.layout.isResizable = !isStatic;

        this.props.selectWidget(null);
        this.props.selectWidget(widget);
        this.props.updateDefault(widget);
    }

    render() {
        if (!this.props.selectedWidget) {
            return (
                <h2 className="p-3">No Widget Selected</h2>
            );
        }
        return (
            <div className="p-3">
                {this.getSettingsTitle()}
                <p>Manage widget layout settings</p>
                <Button onClick={() => this.handleAddWidget(this.props.selectedWidget)}>{this.props.inUse ? "Remove" : "Add"} Widget</Button>
                <Form className="my-2">
                    <Form.Row className="my-4 ml-1">
                        <Form.Check
                            type="checkbox"
                            label="Make Widget static, so it can't be moved or resized."
                            name=""
                            id=""
                            checked={this.props.selectedWidget.layout.static || false}
                            onChange={event => this.setWidgetStatic(event)}
                        />
                    </Form.Row>
                    <hr></hr>
                    <p>Use the grid above to set the position, width and height of the widget.</p>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formGridEmail">
                            <Form.Label>Width</Form.Label>
                            <Form.Control name="width" type="number" placeholder="Set value" readOnly value={this.props.selectedWidget.layout.w || ''} />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridPassword">
                            <Form.Label>Height</Form.Label>
                            <Form.Control name="height" type="number" placeholder="Set Value" readOnly value={this.props.selectedWidget.layout.h || ''} />
                        </Form.Group>
                    </Form.Row>
                    <hr></hr>
                    <p>The Desktop layout is split into 12 columns use the below fields to set the min and max sizes for the widget.</p>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formGridEmail">
                            <Form.Label>Min Width</Form.Label>
                            <Form.Control type="number" placeholder="Set Value" value={this.props.selectedWidget.layout.minW || ''} onChange={event => this.updateMinWidth(event)} />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridEmail">
                            <Form.Label>Max Width</Form.Label>
                            <Form.Control type="number" placeholder="Set Value" value={this.props.selectedWidget.layout.maxW || ''} onChange={event => this.updateMaxWidth(event)} />
                        </Form.Group>
                    </Form.Row>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formGridPassword">
                            <Form.Label>Min Height</Form.Label>
                            <Form.Control type="number" placeholder="Set Value" value={this.props.selectedWidget.layout.minH || ''} onChange={event => this.updateMinHeight(event)} />
                        </Form.Group>

                        <Form.Group as={Col} controlId="formGridPassword">
                            <Form.Label>Max Height</Form.Label>
                            <Form.Control type="number" placeholder="Set Value" value={this.props.selectedWidget.layout.maxH || ''} onChange={event => this.updateMaxWidth(event)} />
                        </Form.Group>
                    </Form.Row>
                </Form>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    var selected = state.selectedWidget.selected;
    var inUse = selected ? selected.inUse : false;

    return { selectedWidget: selected, inUse: inUse }
}

export default connect(
    mapStateToProps,
    {
        updateDefault: updateDefault,
        selectWidget: selectWidget
    }
)(WidgetSettings);